import { Navbar } from '@/components/navbar'
import Image from 'next/image'
import { Button } from '@/components/ui/button'
import { Play, Heart, Share2, Star, Calendar, Film, Activity } from 'lucide-react'
import { notFound } from 'next/navigation'

interface AnimePageProps {
  params: { id: string }
}

interface Genre {
  mal_id: string
  name: string
}

interface AnimeDetail {
  title: string
  otherName?: string
  image?: string
  episodes?: string
  status?: string
  released?: string
  genres?: string[]
  synopsis?: string
}

export default async function AnimePage({ params }: AnimePageProps) {
  const { id: animeId } = params
  if (!animeId) notFound()

  let anime: AnimeDetail | null = null
  try {
    anime = await getAnimeDetail(animeId)
  } catch (err) {
    console.error(err)
    notFound()
  }
  if (!anime) notFound()

  const imageUrl = anime.image || '/placeholder.svg'
  const titleEnglish = anime.otherName || '-'
  const score = '-'
  const episodes = anime.episodes ?? 'TBA'
  const status = anime.status || '-'
  const year = anime.released || 'TBA'
  const genres: Genre[] = (anime.genres || []).map((g) => ({ mal_id: g, name: g }))
  const synopsis = anime.synopsis || '-'
  const airedFrom = 'TBA'
  const airedTo = 'Ongoing'

  return (
    <main className="min-h-screen bg-background overflow-x-hidden">
      <div className="fixed top-0 left-0 w-full z-50">
        <Navbar />
      </div>

      {/* HERO BACKGROUND */}
      <div className="relative h-112 md:h-128 w-full overflow-hidden">
        <Image src={imageUrl} alt="Anime background" fill className="object-cover blur-sm" quality={80} />
        <div className="absolute inset-0 bg-gradient-to-t from-black/40 via-transparent to-transparent" />
      </div>

      {/* CONTENT */}
      <div className="relative z-20 max-w-6xl mx-auto px-4 sm:px-6 lg:px-8 -mt-24 md:-mt-40 grid grid-cols-1 md:grid-cols-3 gap-8">
        {/* POSTER */}
        <div className="relative w-44 md:w-60 mx-auto md:mx-0">
          <div className="shadow-2xl rounded-lg overflow-hidden group transition-transform duration-500 hover:scale-105">
            <Image src={imageUrl} alt={anime.title} width={300} height={400} className="w-full h-auto" />
            <div className="absolute inset-0 bg-black/10 group-hover:bg-black/20 transition-all"></div>
          </div>
        </div>

        {/* TEXT */}
        <div className="md:col-span-2 text-center md:text-left flex flex-col justify-center gap-4">
          <h1 className="text-3xl md:text-4xl font-bold text-white drop-shadow-lg">{anime.title}</h1>
          <p className="text-white/80 text-lg drop-shadow-sm">{titleEnglish}</p>

          {/* STATS */}
          <div className="grid grid-cols-2 md:grid-cols-4 gap-4 mt-4">
            <StatCard icon={<Star className="text-yellow-400" />} label="Rating" value={score} />
            <StatCard icon={<Film className="text-blue-500" />} label="Episodes" value={episodes} />
            <StatCard icon={<Activity className="text-green-500" />} label="Status" value={status} />
            <StatCard icon={<Calendar className="text-purple-500" />} label="Year" value={year} />
          </div>

          {/* ACTION BUTTONS */}
          <div className="flex flex-wrap gap-3 mt-4 justify-center md:justify-start">
            <Button className="bg-primary text-white gap-2 hover:bg-primary/90"><Play size={18} /> Watch Now</Button>
            <Button variant="outline" className="gap-2 border-primary text-primary hover:bg-primary/15"><Heart size={18} /> Add to Favorites</Button>
            <Button variant="outline" className="gap-2 border-primary text-primary hover:bg-primary/15"><Share2 size={18} /> Share</Button>
          </div>
        </div>
      </div>

      {/* GENRES */}
      {genres.length > 0 && (
        <div className="max-w-6xl mx-auto px-4 sm:px-6 lg:px-8 mt-12 mb-6 flex flex-wrap gap-3">
          {genres.map((genre) => (
            <span key={genre.mal_id} className="px-3 py-1 bg-card border border-primary text-primary text-sm rounded-full">{genre.name}</span>
          ))}
        </div>
      )}

      {/* SYNOPSIS */}
      <section className="max-w-6xl mx-auto px-4 sm:px-6 lg:px-8 mb-12">
        <h2 className="text-2xl font-bold text-foreground mb-4">Synopsis</h2>
        <p className="text-muted-foreground leading-relaxed">{synopsis}</p>
      </section>

      {/* AIR DATES */}
      <section className="max-w-6xl mx-auto px-4 sm:px-6 lg:px-8 mb-12">
        <h2 className="text-2xl font-bold text-foreground mb-4">Air Dates</h2>
        <p className="text-muted-foreground">{airedFrom} - {airedTo}</p>
      </section>
    </main>
  )
}

function StatCard({ icon, label, value }: { icon: JSX.Element; label: string; value: string }) {
  return (
    <div className="flex flex-col items-center md:items-start gap-1 bg-card p-3 rounded-lg border border-border shadow-sm">
      {icon}
      <span className="text-sm text-muted-foreground">{label}</span>
      <span className="text-lg font-bold text-primary">{value}</span>
    </div>
  )
}

// Contoh implementasi fetch ke Gogoanime
async function getAnimeDetail(animeId: string): Promise<AnimeDetail> {
  const res = await fetch(`https://api.gogoanime.con/anime-details/${animeId}`)
  if (!res.ok) throw new Error('Failed to fetch anime')
  return res.json()
}
